import React, { useState, useEffect } from 'react'

// import useStore from '../../../StoreProvider'
// import { Select } from '../../../Select'

export default function DetailView() {
	//const [{timeEntry}, dispatch] = useStore()
	// const complete = ({name}) => dispatch({ type: 'TOGGLE', name: 'new Todo' })
	/*
	const {id, description} = timeEntry;

	const [state, setState] = useState({ 
		projectId: 'A',
		projectOptions: [
			{ value: 'A', label: 'Project A'},
			{ value: 'B', label: 'Project B'},
			{ value: 'C', label: 'Project C'},
		],
		tagId: 'X',
		tagOptions: [
			{ value: 'X', label: 'Tag X'},
			{ value: 'Y', label: 'Tag Y'},
			{ value: 'Z', label: 'Tag Z'}
		]			
	});
	*/
	const description = "Answerrrrrrrrrrrr  moj";
	const id = 27;

	useEffect(()=> {
		const divDetailView = document.getElementById('divDetailView');
		divDetailView.addEventListener('animationend', () => {
			if (divDetailView.classList.contains('detail-view-close')) {
				divDetailView.style.zIndex = '0';
				divDetailView.style.left = '100%';
				divDetailView.classList.remove('detail-view-close');
			}
			else {
				divDetailView.style.left = '60%';
			}
		});
	}, []);

	const close = () => {
		// dispatch({ type: 'GO_BACK', page: "LIST_PAGE" })
		const divDetailView = document.getElementById('divDetailView');
		divDetailView.classList.remove('detail-view-open');
		divDetailView.classList.add('detail-view-close');

		document.body.classList.remove('hide-scroll-bar');
	}

	return (
		<div id="divDetailView" className="detail-view">
			{/* <div onClick={complete}>{todo.name}</div> */}
			<button style={{position: 'absolute', right:'10px'}} onClick={close}>X</button>
			<h2>Time Entry</h2>
			<div>
				Id: { id}
			</div>
			<div style={{ width: '100%' }}>
				<div>					
					Description: <textarea value={description} onChange={()=>{}}></textarea>
				</div>

				<div style={{padding: '5px'}}>
					Project:
					{/* <Select
						options={state.projectOptions}
						value={state.projectId}
						onChange={(selectedOption) => {
							setState({...state, projectId: selectedOption.value})
						}}
					/>			 */}
				</div>

				<div style={{padding: '5px'}}>
					Tags:
					{/* <Select
						options={state.tagOptions}
						value={state.tagId}
						onChange={(selectedOption) => {
							setState({...state, tagId: selectedOption.value})
						}}
					/>			 */}
				</div>

			</div>
		</div>
	)
}