import * as React from 'react';
import { useParams } from 'react-router-dom' // useRouteMatch

import { IQuestion, IComponentProps } from '../types';


import { AutoSuggest } from '../../components/AutoSuggest';
import { Form } from './Form'


type SupportParams = {
	tekst: string;
};
  
const PromoterPage: React.FC<IComponentProps> = (props: IComponentProps) => {
	let { tekst } = useParams<SupportParams>();
	const { questionGroups, question, questionAnswers, answers, formMode, onSelectQuestion, cancel, saveForm, canEdit,
		removeQuestionAnswer, assignQuestionAnswer, setIsDetail } = props;
	 
    return (
      <div className="name-container">

			<div className="two-columns">
				<div className="a">
					{/* Support Page tekst: {tekst} */}
					<AutoSuggest
						questionGroups={questionGroups}
						tekst={tekst}
						onSelectQuestion={onSelectQuestion} 
					/>
				</div>
				<div className="b">
					{questionGroups && question &&
						<div style={{border: '1px solid silver', borderRadius: '5px', padding: '10px'}}>
							<h4 style={{marginTop: 0}}>Question</h4>
							<Form 
								question={question}
								questionAnswers={questionAnswers}
								answers={answers}
								formMode={formMode}
								cancel={cancel}
								saveForm={(question: IQuestion) => saveForm(question, formMode)}
								canEdit={canEdit}
								removeQuestionAnswer={removeQuestionAnswer}
								assignQuestionAnswer={assignQuestionAnswer}
								setIsDetail={setIsDetail}
							/>
						</div>
					}
				</div>
			</div>
			
      </div>
    );
  }

export default PromoterPage

