import * as React from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWindowClose } from '@fortawesome/free-solid-svg-icons'
import { faPlus } from '@fortawesome/free-solid-svg-icons'


import { IQuestion } from '../types';
import { IAnswer } from '../../Answers/types';
import { AutoSuggestAnswer } from '../../components/AutoSuggestAnswer';

interface IProps {
	question: IQuestion,
	questionAnswers: IAnswer[],
	answers?: IAnswer[],
	canEdit: boolean,
	formMode: string,
	removeQuestionAnswer?: (groupId: number, questionId: number, answerId: number) => void,
	assignQuestionAnswer?: (groupId: number, questionId: number, answerId: number, tekst?: string) => void,
	setAnswerText?: (val: string) => void,
	setIsDetail: (isDetail: boolean) => void
}

const QuestionAnswers: React.FC<IProps> = (props: IProps) => {
	const { 
		question, 
		questionAnswers, 
		answers, 
		canEdit, 
		formMode, 
		removeQuestionAnswer, assignQuestionAnswer, setIsDetail 
	} = props; 
	const answersUnassigned = answers?.filter(a => !question.answers.includes(a.answerId))

	const [tekst, setTekst] = React.useState('');
	const setAnswerText = (val: string) => {
		console.log('setAnswerText', question.groupId, question.questionId, val)
		setTekst(val);
	}

	const assignQuestionAnswerTekst = () => {
		if (assignQuestionAnswer) {
			//storeAnswer({ answerId: -1, text: tekst }, 'add')
			assignQuestionAnswer(
				question.groupId,
				question.questionId, 
				-1,
				tekst
			);
		}
	}

	const goDetail = (isDetail: boolean) => {
		setIsDetail(isDetail);
		//dispatch({ type: 'GO_DETAIL', timeEntry });

		const divDetailView = document.getElementById('divDetailView');
		console.log('divDetailView', divDetailView)
		divDetailView!.classList.add('detail-view-open');
		divDetailView!.style.zIndex = '3';
		//document.body.classList.add('hide-scroll-bar');
  	}	


    return (
      <div className="name-container">
			{/* { questionAnswers.length === 0 && 
				<div>
					No answers yet
				</div>
			} */}
			{ questionAnswers.length > -1 && 
				<>
				<table>
					<thead>
						<tr>
							<th>{ questionAnswers.length === 0 ? 'No answers yet' : 'Answers'}</th>
							{ canEdit && formMode !== 'display' && <th></th> }
						</tr>
					</thead>
					<tbody>
						{ questionAnswers.map(answer => 
							<tr key={answer.answerId}>
								<td className="name">
									{answer.text}
								</td>
								{ canEdit && formMode !== 'display' &&
									<td>
										<button className="button-remove" title="Remove Answer" 
											onClick={(e) => { 
												e.stopPropagation();
												e.preventDefault();
												removeQuestionAnswer!(question.groupId, question.questionId, answer.answerId)
												}}>
											<FontAwesomeIcon icon={faWindowClose}  color='lightblue' />
										</button>
									</td>
								}
							</tr>
						)}
						{canEdit && formMode !== 'display' && <tr>
							<td>
								<AutoSuggestAnswer
									question={question}
									answersUnassigned={answersUnassigned!}
									assignQuestionAnswer={assignQuestionAnswer!}
									setAnswerText={setAnswerText}
								/>
							</td>
							<td>
								<button className="button-edit" title="Add a new Answer" onClick={() => goDetail(true)}>
									<FontAwesomeIcon icon={faPlus} color='lightblue' />
								</button>
							</td>
							</tr>}
					</tbody>
				</table>
				</>
			}
      </div>
    );
  }

export default QuestionAnswers

